目前本網消費開立之「三聯電子發票」為會員載具，尚未開放共通性載具選項。`,
                alert: ''
            },
            {
                id: 2,
                title: '發票可以指定日期或開立其它品名嗎?',
                answer: '因發票採用系統化自動開立，因此發票上的品名將依實際訂購的商品名稱開立，請恕無法指定開立其它品名或指定開立日期。',
                alert: ''
            },
            {
                id: 3,
                title: '購物發票，是否可開立為多張發票?',
                answer: '因發票採用系統化自動開立，故一筆訂單對應一張發票，請恕無法分別開立多張。如有分別開立需求，請將商品分別下單。',
                alert: ''
            }
        ]
    }
]
export default {

    data() {
        return {
            data: dummyData,
            secectedId: 1,
            selectedData: {}
        }
    },
    created() {
        this.selectedData = dummyData.filter(item => item.id === this.secectedId)[0]
    },
    methods: {
        handleSelected(id) {
            this.secectedId = id
            this.selectedData = dummyData.filter(item => item.id === this.secectedId)[0]
        },
        handleChange(e) {
            this.secectedId = e.target.value
            this.selectedData = this.selectedData = dummyData.filter(item => item.id === Number(e.target.value))[0]
        
        },
        goToQuestion(id) {
            const height = $(`#${id}`).offset()?.top
        $('html, body').animate({
            scrollTop: height,
        }, 200)

        }
    }
}
</script>

<style lang="scss">
.container-wrapper {
    min-height: calc(100vh - 100px);

    img {
        height: 300px;
        object-fit: cover;
    }

    .sidebar {
        h2 {
            border-left: 5px solid #00457c;
        }
        .list-item {
            font-weight: 600;

            &:hover {
                cursor: pointer;
                background: #00457c;
                color: #fff;
            }

            &.active {
                background: #00457c;
                color: #fff;

            }
        }
        .select-wrapper {
            background-color: #00457c;
            padding: 0.5rem;
            select {
                color: #fff;
                background-color: #00457c;
                font-weight: 600;
            }
        }
    }

    .question-title {
        font-weight: 600;
        &:hover {
            cursor: pointer;
        }
    }

    .question-answer {
        white-space: pre-line;
        line-height: 2rem;
    }
}
</style>